import Layout from '../../components/Layout';
import Sidebar from './sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { selectUser } from '../../redux/userReducer';
import { useSelector, useDispatch } from 'react-redux';
import { useEffect, useState, createRef } from 'react';
import { web3Service } from '../../services/web3.service';
import { useNavigate } from 'react-router-dom';
import { COIN_TYPES } from '../../config/constants';
import { openSnackBar } from '../../redux/snackBarReducer';
import { selectFee, updateFeeData, selectAdminData, getFee } from '../../redux/adminReducer';
import AdminWithdrawModal from '../admin_modals/withdraw.modal';

const Wallet = ({
  searchBar = true,
  homeBtn = true,
  walletNav = true,
  faqBtn = false
}) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const seluser = useSelector(selectUser);
  const adminFee = useSelector(selectFee);
  const adminData = useSelector(selectAdminData);
  const [fee, setFee] = useState(0);
  const [walletAddress, setWalletAddress] = useState("");
  const [walletBalance, setWalletBalance] = useState(0);
  const [transaction, setTransaction] = useState([]);
  const [loading, setLoading] = useState(false);
  const [loadingBalance, setLoadingBalance] = useState(false);
  const [type, setType] = useState(0);
  const [withdrawModal, setWithdrawModal] = useState(false);

  const getWalletAddress = (type) => {
    var addr = seluser.is_admin ? adminData.walpublic:seluser.walpublic;
    switch (type) {
      case 0:
        addr = seluser.is_admin ? adminData.btcwalpublic:seluser.btcwalpublic;
        break;
      case 1:
        break;
      case 2:
        break;
      case 3:
        break;
      case 4:
        break;
      case 5:
        break;
    }
    setWalletAddress(addr);
    getTransactions(type, addr);
    getWalletBalance(type, addr);
  }

  const getWalletBalance = async (type, addr) => { 
    setLoadingBalance(true);  
    const ret = await web3Service.getWalletBalance(addr, type);
    const balance = ret.data.ret;
    setWalletBalance(balance);
    setLoadingBalance(false);  
  }

  const getTransactions = async (type, addr) => {    
    let res;
    setLoading(true);   
    setTransaction([]);
    switch (type) {
      case 0:        
        try {
          res = await web3Service.trackTransactions(addr, 0, type);
          setTransaction(res.data.ret);
        } catch {          
          setLoading(false);
        }
        break;
      case 1:
        try {
          res = await web3Service.trackTransactions(addr, 0, type);
          setTransaction(res.data.ret);
        } catch {          
          setLoading(false);
        }
        break;
      case 2:
        break;
      case 3:
        break;
      case 4:
        try {
          res = await web3Service.trackTransactions(addr, 0, type);
          setTransaction(res.data.ret);
        } catch {          
          setLoading(false);
        }
        break;
      case 5:
        break;
    }
    setLoading(false);
  }

  const handleCopyClipboard = () => {
    navigator.clipboard.writeText(walletAddress);
    dispatch(openSnackBar({ message: "Copied to clipboard successfully", status: 'success' }));
  }

  const handleSetFee = () => {
    dispatch(updateFeeData(fee));
  }

  useEffect(() => {
    getWalletAddress(type);
  }, [type])

  useEffect(() => {
    setFee(adminFee);
  }, [adminFee])

  useEffect(() => {
    dispatch(getFee());
  }, [])

  return (
    <Layout classes={'bg-[#404040] h-full'} searchBar={searchBar} walletNav={walletNav} faqBtn={faqBtn}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                {homeBtn && <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>}
                <p className='ml-2 font-bold'>Wallet</p>
              </div>
              <div className='w-full flex justify-between text-gray-100 text-xl mb-2 bold px-[2rem] py-[1rem]'>
                <div className="w-half flex items-center ">
                  <p className='font-bold'>Coin Type: </p>
                  <select
                    className="bg-[#404040] font-bold border-b border-gray-300 text-gray-100 text-xl block w-[10rem] p-2.5 focus:outline-none ml-[1rem]"
                    onChange={(e) => setType(parseInt(e.target.value))}
                  >
                    <option value={0}>{COIN_TYPES[0]}</option>
                    <option value={1}>{COIN_TYPES[1]}</option>
                    <option value={4}>{COIN_TYPES[4]}</option>
                  </select> 
                </div>
                {seluser.is_admin && <div className="w-half flex items-center ml-[4rem]">
                  <p className='font-bold'>Client Fee: </p>
                  <input 
                    type="number" 
                    max={10} 
                    min={3} 
                    className="ml-[1rem] appearance-none bg-transparent border-b border-gray-300 w-[8rem] text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none"
                    value={fee}
                    onChange={(e) => setFee(parseFloat(e.target.value))}
                  />
                  <p className='font-bold mr-2'>{'(%)'}</p>
                  <button 
                      className={`w-[8rem] text-xl leading-[2.6rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800`}
                      onClick={() => handleSetFee()}
                  >Set</button>
                </div>}
              </div>
              <div className='w-full flex justify-between text-gray-100 text-xl mb-2 bold px-[2rem] pb-[1rem]'>
                <div className="w-[60%] flex items-center">
                  <div onClick={() => handleCopyClipboard()} className='text-left leading-[1.2rem] select-none'>
                    Address: <span className="font-bold hover:text-gray-300 cursor-pointer hover:underline">{walletAddress ? walletAddress.substr(0, 4) + "..." + walletAddress.substr(walletAddress.length - 4, 4) : ''}</span>
                  </div>
                  <div className='text-left leading-[1.2rem] select-none ml-[4rem]'>
                    Balance: <span className="font-bold">{loadingBalance ? 'loading...' : walletBalance}</span>
                  </div>
                </div>
                <div className="w-[40%] flex items-center justify-end">
                  <button 
                      className={`w-[8rem] text-xl leading-[2.6rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800`}
                      onClick={()=>setWithdrawModal(true)}
                  >Withdraw</button>
                </div>
              </div>
              <div className='w-full flex text-gray-100 leading-[1.8rem]'>
                <p className='w-[15%] pl-6'>Txn Hash</p>
                <p className='w-[12%] text-left'>Method</p>
                <p className='w-[15%] text-left'>Age</p>
                <p className='w-[15%] text-left'>From</p>
                <p className='w-[18%] text-left'>To</p>
                <p className='w-[10%] text-left'>Value</p>
                <p className='w-[15%] text-left'>Token</p>
              </div>
              <div className='w-full h-[calc(100vh-21rem)] carousel mt-[1rem] text-gray-100 text-xl'>
                {loading ? <div className="w-full text-center">Loading...</div> : transaction.length > 0 ? <div className='w-full h-[calc(100vh-17em)] overflow-y-auto'>
                  {
                    transaction.map((t, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 leading-[1.4rem] bg-[#333333] border-b border-[#282828]'>
                          <p className='w-[15%] text-left'>{t.blockHash.substr(0, 8) + "..."}</p>
                          <p className='w-[12%] text-left'>{t.functionName}</p>
                          <p className='w-[15%] text-left'>{t.timeStamp}</p>
                          <p className='w-[15%] text-left'>{t.from.substr(0, 8) + "..."}</p>
                          <p className='w-[18%] text-left'>{t.to.substr(0, 8) + "..."}</p>
                          <p className='w-[10%] text-left'>{(t.value / 10**18).toFixed(8)}</p>
                          <p className='w-[15%] text-left'>Token</p>
                        </div>
                      )
                    })
                  }
                </div> : <div className="w-full text-center">Empty Data</div>}
              </div>
            </div>
            {(seluser && seluser.role === 'merchant') && <Sidebar />}
        </div>
        <AdminWithdrawModal isModal={withdrawModal} setIsModal={setWithdrawModal} walletBalance={walletBalance}/>
    </Layout>
  );
}

export default Wallet;