import { Fragment, useState, useEffect } from 'react';
// import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import { openSnackBar } from '../../redux/snackBarReducer';
import { ethers } from "ethers"

const AdminWithdrawModal = ({isModal, setIsModal, walletBalance, coinType}) => {
  // const navigate = useNavigate();
  const dispatch = useDispatch();
  const [address, setAddress] = useState('');
  const [balance, setBalance] = useState(0);

  const checkWithdrawValidation = (index) => {
    const ret = {message: "", status: false};
    if (address.length === 0) {ret.message = "Please input receipt address."; ret.status = false;}
    else if (!ethers.utils.isAddress(address)) {ret.message = "Should be available receipt address."; ret.status = false;}
    else if (balance === 0) {ret.message = "Withdraw ammount could not be zero."; ret.status = false;}
    else if (balance < 0) {ret.message = "Withdraw ammount could not be negative value."; ret.status = false;}
    else if (balance > walletBalance) {ret.message = `Withdraw ammount could not be greater than ${walletBalance}.`; ret.status = false;}
    else { ret.message = ""; ret.status = true;}

    return ret;
  }

  const handleConfirm = () => {
    const checking = checkWithdrawValidation();
    if (!checking.status) {
      dispatch(openSnackBar({ message: checking.message, status: 'error' }));  
      return;
    }
    setIsModal(false);
  }

  const handleMaxBalance = () => {
    setBalance(walletBalance);
  }

  useEffect(() => {
    if (isModal) {
      setAddress('');
      setBalance(0);
    }
  }, [isModal])

  return (
    <Transition appear show={isModal} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full" onKeyUp={(e) => {if (e.code === 'Enter' || e.code === 'NumpadEnter') { handleConfirm()}} }>
              <div className="relative bg-[#404040] w-[30vw] max-w-[1000px] min-w-[380px] min-h-[160px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}
                <div className=''>
                  <h1 className='text-xl font-bold text-left text-[#D9D9D9] select-none'>Withdraw:</h1>
                </div>
                {/* CONTENTS */}
                <div className="min-h-[2rem] pb-[0.5rem]">
                  <div className="flex flex-col my-[1rem]">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 text-left">
                      Receipt Address:
                    </label>
                    <input 
                        className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                        type="text"
                        value={address}
                        onChange={e => setAddress(e.target.value)} />
                  </div>
                  <div className="flex flex-col my-[1rem] mb-[2.6rem]">
                    <label className="block uppercase tracking-wide text-gray-100 mb-1 text-left">
                      Withdraw Amount:
                    </label>
                    <div className="flex items-center">
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                          type="number"
                          value={balance}
                          min={0}
                          onChange={e => setBalance(e.target.value)} />
                      <button 
                          className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}                                      
                          onClick={()=>handleMaxBalance()}
                      >Max</button>
                    </div>
                  </div>
                </div>
                {/* CONTROLS */}
                <div className='absolute right-6 bottom-4'>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#404040] border border-[#D9D9D9] text-gray-100 mr-6`}            
                      onClick={() => setIsModal(false)}
                  >Cancel</button>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                      onClick={() => handleConfirm()}
                  >Withdraw</button>
                </div>         
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default AdminWithdrawModal;